* {
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
  background-image: url('https://www.designbolts.com/wp-content/uploads/2012/12/Noisy-Grid-Seamless-Pattern-For-Website-Background.jpg');
  position: fixed;
  min-width: 100%;
  min-height: 100%;
  background-position: center;
}

.App-header {
  font-family: 'Teko', sans-serif;
  font-size: 50px;
}

.app-container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.canvas {
  border: 1px solid black;
  background-color: grey;
}

.canvasSection {
  display: flex;
  flex-direction: column;
}

.gameSection {
  display: flex;
  flex-direction: row;
}

.rulesSection {
  display: flex;
  flex-direction: column;
}

.buttonSide {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding-left: 20px;
}

.buttonBottom {
  display: flex;
  justify-content: space-evenly;
  padding-top: 20px;
  align-items: center;
}

.ul {
  display: flex;
  align-items: baseline;
  flex-direction: column;
}

.li {
  font-size: 50px;
}

.h1 {
  font-family: 'Teko', sans-serif;
  font-size: 50px;
}

.colorCenter {
  padding-top: 10px;
}
